.tec-skills {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    padding: 0 10%;
    flex-wrap: wrap;
    box-sizing: content-box;

    img {
        padding-top: 20px;
        width: 6rem;
        filter: drop-shadow(0 0 0.55rem rgb(246, 166, 6));
        transform: rotate(10deg);
    }
}

.rotating-image {
    animation: rotate 5s ease-out alternate backwards;

}

@keyframes rotate {
    0% {
        transform: rotateY(0deg);

    }

    100% {
        transform: rotateY(-45deg);

    }
}






